<template>
  <el-form
    v-model="form"
    status-icon
    :rules="rules"
    ref="form"
    label-width="100px"
    class="login_container"
  >
    <h3 class="login_title">系统登录</h3>
    <el-form-item
      label="用户名"
      label-width="80px"
      prop="username"
      class="username"
    >
      <el-input
        type="input"
        v-model="form.username"
        auto-complete="off"
        placeholder="请输入账号"
      ></el-input>
    </el-form-item>
    <el-form-item label="密码" label-width="80px" prop="password">
      <el-input
        type="password"
        v-model="form.password"
        auto-complete="off"
        placeholder="请输入密码"
      ></el-input>
    </el-form-item>
    <el-form-item class="login_submit">
      <el-button type="primary" @click="login" class="login_submit"
        >登录</el-button
      >
    </el-form-item>
  </el-form>
</template>
<script>
// import Mock from "mockjs";
import { getMenu } from "../../api/data";
export default {
  name: "Login",
  data() {
    return {
      form: {},
      rules: {
        username: [
          { requried: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, message: "用户名长度不能小于3位", trigger: "blur" },
        ],
        password: [{ requried: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    login() {
      //判断登录用户是管理员还是普通用户来返回对应的显示菜单
      getMenu(this.form).then(({ data }) => {
        if (data.code === 20000) {
          //清除之前的menu
          this.$store.commit("clearMenu");
          //重新设置menu
          this.$store.commit("setMenu", data.data.menu);
          //缓存token保存登录状态
          this.$store.commit("setToken", data.data.token);
          //动态添加路由
          this.$store.commit("addMenu", this.$router);
          //设置用户
          this.$store.commit("setUser", this.form.username);
          //跳转
          this.$router.push({ name: "home" });
        } else {
          this.$message.warning(data.data.message);
        }
      });

      //暂时在前端利用mockjs模拟后端生成的token
      // const token = Mock.Random.guid();
      // this.$store.commit("setToken", token);
      // this.$router.push({ name: "home" });
    },
  },
};
</script>

<style lang="less" scoped>
.login_container {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background-color: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.login_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
.login_submit {
  margin: 10px auto 0px auto;
}
</style>
